.f-field-pass {
  position: relative;
  display: flex;
  column-gap: 8px;
  margin: 0;
  padding: 0;
  border: 0;
}

.f-field-pass__visible {
  transition-timing-function: ease-out;
  transition-duration: 0.2s;
  transition-property: color;
  position: absolute;
  top: 50%;
  left: calc(100% + 12px);
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  color: #000080;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transform: translateY(-50%);
}
.f-field-pass__visible svg {
  fill: currentcolor;
}
.f-field-pass__visible:hover, .f-field-pass__visible:focus {
  color: #0000e6;
}
.f-field-pass__visible svg:last-child {
  display: none;
}
.f-field-pass__visible.show svg:first-child {
  display: none;
}
.f-field-pass__visible.show svg:last-child {
  display: block;
}

.f-field-select {
